import { memo, FC } from 'react';
import { Link } from 'react-router-dom';
import styled, { css } from 'styled-components';
import { mediaQuery } from '../utils/helpers';

type Appearance = 'primary' | 'default';

function getButtonAppearance(appearance: Appearance, outline: boolean) {
  switch (appearance) {
    case 'primary':
      return css`
        ${(p) =>
          outline
            ? ` color: ${p.theme.colors.primary}; 
                border-color: ${p.theme.colors.primary}`
            : ` background-color: ${p.theme.colors.primary}; 
                border-color: ${p.theme.colors.primary};
                color: white;
                
                &:hover {
                  background-color: ${p.theme.colors.primaryHover}; 
                }`}
      `;

    default:
      return '';
  }
}

function getButtonDisplay(display: Partial<Record<BerakPoints, string>>) {
  if (!display || Object.keys(display).length === 0) {
    return '';
  }

  return Object.keys(display).reduce((style, breakpoint: BerakPoints) => {
    return `${style} ${mediaQuery(breakpoint)} { display: ${
      display[breakpoint]
    }};`;
  }, '');
}

interface IButtonProps {
  appearance?: Appearance;
  outline?: boolean;
  display?: Partial<Record<BerakPoints, string>>;
}

export const Button = styled.button<IButtonProps>`
  padding: 0.5rem 1rem;
  border: 1px solid ${(p) => p.theme.colors.border};
  background-color: transparent;
  border-radius: 6px;
  cursor: pointer;
  text-decoration: none;

  &:not(:first-of-type) {
    margin-left: 0.5rem;
  }

  &:hover {
    border-color: ${(p) => p.theme.colors.primary};
  }

  &:disabled {
    background-color: ${(p) => p.theme.colors.border};
    border-color: ${(p) => p.theme.colors.border};
    cursor: not-allowed;
  }

  ${(p) => p.appearance && getButtonAppearance(p.appearance, p.outline)}
  ${(p) => p.display && getButtonDisplay(p.display)}
`;

const LinkStyle = styled(Link)`
  text-decoration: none;
  color: inherit;

  &::after {
    display: none;
  }
`;

interface IProps
  extends React.ButtonHTMLAttributes<HTMLButtonElement>,
    IButtonProps {
  to: string;
}

export const LinkButton: FC<IProps> = memo(({ to, ...props }) => {
  return (
    <LinkStyle to={to}>
      <Button
        type="button"
        {...props}
        appearance={props.appearance}
        outline={props.outline}
      />
    </LinkStyle>
  );
});

LinkButton.defaultProps = {
  appearance: 'primary',
  outline: true,
};

export const IconButton = styled(Button)`
  border: 0;

  > svg {
    width: 1.2em;
    height: 1.2em;
    display: inline-block;
    vertical-align: middle;
  }
`;
