import loadable from '@loadable/component';
import { Placeholder } from './components/Placeholder';

export const loadableOptions = {
  fallback: (
    <>
      <Placeholder height={40} />
      <Placeholder height={40} />
      <Placeholder height={40} />
    </>
  ),
};
export const routes = [
  {
    path: '/',
    exact: true,
    component: loadable(() => import('./pages/HomePage'), loadableOptions),
  },
  {
    path: '/nomination',
    exact: true,
    component: loadable(() => import('./pages/Nomination'), loadableOptions),
  },
  {
    path: '/nomination/add',
    exact: true,
    component: loadable(
      () => import('./pages/NominationAdding'),
      loadableOptions
    ),
  },
];
