import { createContext, memo, useCallback, useContext, useState } from 'react';

interface ContextProps {
  nominees: Nominee[];
  addNominee(payload: Nominee): void;
}

const Context = createContext<ContextProps>(null);

export const NomineeProvider = memo((props) => {
  const [nominees, setNominees] = useState([]);
  const handleAddNominee = useCallback(
    (data: Nominee) => {
      setNominees([data, ...nominees]);
    },
    [nominees]
  );

  return (
    <Context.Provider value={{ nominees, addNominee: handleAddNominee }}>
      {props.children}
    </Context.Provider>
  );
});

export const useNomineesContext = () => {
  const context = useContext(Context);
  return [context.nominees, context.addNominee] as [
    ContextProps['nominees'],
    ContextProps['addNominee']
  ];
};
