import formatDate from 'date-fns/format';

const SMALL_UP = 567;
const MEDIUM_UP = 768;
const LARGE_UP = 992;

const SCREEN_SIZES: Record<BerakPoints, string> = {
  xs: '0px',
  sm: `${SMALL_UP}px`,
  md: `${MEDIUM_UP}px`,
  lg: `${LARGE_UP}px`,
};

export const mediaQuery = (breakpoint: BerakPoints) =>
  `@media (min-width: ${SCREEN_SIZES[breakpoint] || 0})`;

export const isDesktop = (width: number) => width >= MEDIUM_UP;

// eslint-disable-next-line no-extend-native
Date.prototype.toCommonDateFormat = function () {
  return formatDate(this, 'dd MMM yyyy');
};

// expected dateStr is DD/MM/YYYY
export function dateFormatter(dateStr: string) {
  if (!dateStr) return '';

  const [d, m, y] = dateStr.split('/').map((no) => parseInt(no, 10));
  return new Date(y, m - 1, d).toCommonDateFormat();
}
